import styled from 'styled-components';
import { Link } from 'react-router-dom'

export default styled(Link)`
	text-decoration: none;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	text-align: center;
	color: inherit;
	padding: 10px;
	border: 1px solid black;

`


